body {
  font-family: Nunito, sans-serif;
}

.cat__info {
  height: 500px;
  flex-direction: row;
  gap: 50px;
  margin-left: 0;
  display: flex;
}

.breed-select {
  border: 2mm ridge rgba(42, 43, 7, .6);
  font-size: 20px;
  font-weight: 500;
}

.loader {
  width: 56px;
  height: 56px;
  perspective: 500px;
  background: #be3d3d;
  border-radius: 4px;
  position: relative;
}

.loader:before {
  content: "";
  width: 24px;
  height: 24px;
  transform-origin: 100% 100%;
  background: #ff3d00;
  border-radius: 50%;
  animation: flip 2s linear infinite;
  position: absolute;
  top: 2px;
  left: 2px;
}

@keyframes flip {
  0%, 100% {
    transform: rotateX(0deg)rotateY(0deg);
  }

  25% {
    transform: rotateX(0deg)rotateY(-180deg);
  }

  50% {
    transform: rotateX(-180deg)rotateY(-180deg);
  }

  75% {
    transform: rotateX(-180deg)rotateY(0deg);
  }
}

